import React from "react";
import {
  CRow,
  CCard,
  CCardBody,
  CCol,
  CCardHeader,
  CButton,
} from "@coreui/react";
import { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import axios from "axios";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { MdOutlineModeEdit } from "react-icons/md";
import { AiOutlineDelete } from "react-icons/ai";
import Swal from "sweetalert2";
import { HiArrowSmLeft  } from "react-icons/hi";
// or

const State = () => {
  const [state, setState] = useState([]);
  const token =
    typeof window !== "undefined" ? localStorage.getItem("status") : null;

  const Toast = Swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", Swal.stopTimer);
      toast.addEventListener("mouseleave", Swal.resumeTimer);
    },
  });

  const [filterText, setFilterText] = useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = useState(false);

  // --------------------------------------------------FETCH ALL RECRUITER-------------------------------------------------

  useEffect(() => {
    axios
      .get("stateList", { headers: { Authorization: "Bearer " + token } })
      .then((res) => {
        console.log(res);
        
        res.data.status == 200 ?   setState(res.data.data):  setState([])
      });
  }, [token]);

  // ---------------------------------------------------------------------------------------------------------------------

  // --------------------------------------------------DELETE RECRUITER---------------------------------------------------

  const deleteHandler = (row) => {
    axios
      .get("stateDelete/" + row.id, {
        headers: {
          Accept: "application/json",
          Authorization: "Bearer " + token,
        },
      })
      .then((result) => {
        Toast.fire({
          icon: "success",
          title: result.data.message,
        });

        console.log(result);
        let newArr = state.filter((items) => {
          return row.id !== items.id;
        });
        setState(newArr);
        console.log(newArr);
        // window.location.reload(false);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  // ---------------------------------------------------------------------------------------------------------------------

  // ------------------------------------------------RECRUITER TABLE COLUMN-----------------------------------------------
  // const data = [
  //   {

  //       phoneno: "1234567890",
  //       username: "abc1234",
  //       name: "1234abc",
  //       city: "bhopal",
  //       gender: "male",

  //     },
  // ]

  const columns = [
    {
      name: "#",
      selector: (row, index) => index + 1,
      width: "120px",
    },

    {
      name: "State Name",
      selector: (row) => (
        <span className="text-capital fs-15">{row.state_name}</span>
      ),
      sortable: true,
      maxWidth: "440px",
    },

    {
      name: "Action",
      selector: (row) => (
        <div>
          <Link to={`/edit-state/${row.id}`}>
            <CButton className="border-b bg-black  m-1">
              <MdOutlineModeEdit size={20} />
            </CButton>
          </Link>
          <CButton
            className="border-b bg-black m-1"
            onClick={() => {
              if (window.confirm("Are you sure you wish to delete this item?"))
                deleteHandler(row);
            }}
          >
            <AiOutlineDelete size={20} />
          </CButton>
        </div>
      ),
      width: "140px",
      sortable: false,
    },
  ];

  const filteredItems = state.filter(
    (item) =>
      item.state_name &&
      item.state_name.toLowerCase().includes(filterText.toLowerCase())
  );

  const handleClear = () => {
    if (filterText) {
      setResetPaginationToggle(!resetPaginationToggle);
      setFilterText("");
    }
  };

  const TextField = styled.input`
    height: 32px;
    width: 180px;
    border-radius: 3px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 1px solid #e5e5e5;
    padding: 0 32px 0 16px;
    &:hover {
      cursor: pointer;
    }
  `;
  return (
    <>
      <div className="px-2">
        <CCol xs={12}>
          <CCard className="mb-4 my-2">
            <CCardHeader>
            {filterText && (
                <HiArrowSmLeft  style={{width:30, height:30}}
                                onClick={handleClear}/>
                                
                              
                            )}
              <strong>State</strong>
            </CCardHeader>
            <CCardBody>
              <CRow>
                <CCol lg={12}>
                  <CCard color="light" textColor="black" className="mb-3">
                    <CCardHeader>
                      <div className="row ">
                        <CCol sm={3}>
                          <div className="text-center">
                            <TextField
                              type="text"
                              placeholder="Search...."
                              className="mt-1 mb-2"
                              value={filterText}
                              autoFocus
                              onChange={(e) => setFilterText(e.target.value)}
                            />
                            
                       
                       
                           
                          </div>
                          
                        </CCol>
                        <CCol sm={6}></CCol>
                        <CCol sm={3}>
                          <div className="text-center">
                            <Link to="/add-state">
                              <CButton className="bg-black text-white border-b px-3 py-1 br-10 m-1">
                                + Add State
                              </CButton>
                            </Link>
                          </div>
                        </CCol>
                      </div>
                    </CCardHeader>
                    <CCardBody>
                      <DataTable
                        columns={columns}
                        data={filteredItems}
                        defaultSortFieldId
                        // fixedHeaderScrollHeight="300px"
                        fixedHeader
                        responsive
                        pagination={10}
                        subHeaderAlign="right"
                        highlightOnHover
                      />
                    </CCardBody>
                  </CCard>
                </CCol>
              </CRow>
            </CCardBody>
          </CCard>
        </CCol>
      </div>
    </>
  );
};

export default State;
